import "./Project.css";
const projects = [
  {
    title: "Project-1",
    name: "MYTHERESA.com clone",
    img: "data:image/jpeg;base64,",
    glink: "https://github.com/sanju682295/dark-van-7997/tree/master/HTML",
    llink: "https://resplendent-mermaid-59b026.netlify.app/index.html",
    tech: "HTML | CSS | JavaScript",
    para: "Mytheresa is an online shopping destination for children's, men's, and women's luxury fashion and lifestyle design.",
  },
  {
    title: "Project-2",
    name: "LoseIt.com clone",
    img: "data:image/png;base64,",
    glink: "https://github.com/ranjanpalai69/malicious-thrill-8470",
    llink: "https://brilliant-moxie-01b056.netlify.app/landing%20page2.2/landing.html",
    tech: "HTML | CSS | JavaScript",
    para: "Lose It! is a calorie counter & food diary diet app that helps you stick to your diet and achieve weight loss that fits!",
  },
  {
    title: "Project-3",
    name: "StyleCraze.com clone",
    img: "data:image/jpeg;base64,",
    glink: "https://github.com/sanju682295/gutsy-sheet-7629",
    llink: "https://crazystyle.vercel.app/",
    tech: "ReactJs | CSS | ChakraUI | JavaScript",
    para: " Women's Fashion, Beauty And Wellness Community to get solutions to all your Beauty queries and stay upto on the latest Beauty Trends.",
  },
];
const Projects = () => {
  return (
    <section id="projects">
      <h1>My Projects</h1>
      <div className="project-container">
        {projects.map((pro) => (
          <div className="project-card">
            <div className="image">
              <img src={pro.img} alt="" />
            </div>
            <div style={{ textAlign: "center" }} className="project-details">
              <h3 style={{ color: "black",marginBottom:"20px" }}>{pro.name}</h3>
              <div style={{display:"flex",justifyContent:"center",gap:"3px",width:"98%"}}>
             <h3 style={{color:"black"}}>Tech stack:- {pro.tech}</h3>
              </div>
              <br />
              <p
                style={{
                  color: "black",
                  marginTop: "0px",
                  marginLeft: "20px",
                  marginRight: "20px",
                  textAlign: "justify",
                }}
              >
                {pro.para}
              </p>
            </div>
            <div
              className="links"
              style={{
                textAlign: "center",
                display: "flex",
                justifyContent: "space-around",
                paddingTop: "15%",
                width:"50%",
                margin:"auto"
              }}
            >
                <a href={pro.glink}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="30"
                height="30"
                fill="currentColor"
                class="bi bi-github"
                viewBox="0 0 16 16"
                color="black"
                
              >
                <path d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.012 8.012 0 0 0 16 8c0-4.42-3.58-8-8-8z" />
              </svg>
              </a>
              <a href={pro.llink} target="blank">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="30"
                height="30"
                fill="currentColor"
                class="bi bi-link"
                viewBox="0 0 16 16"
                color="black"
                
              >
                <path d="M6.354 5.5H4a3 3 0 0 0 0 6h3a3 3 0 0 0 2.83-4H9c-.086 0-.17.01-.25.031A2 2 0 0 1 7 10.5H4a2 2 0 1 1 0-4h1.535c.218-.376.495-.714.82-1z" />
                <path d="M9 5.5a3 3 0 0 0-2.83 4h1.098A2 2 0 0 1 9 6.5h3a2 2 0 1 1 0 4h-1.535a4.02 4.02 0 0 1-.82 1H12a3 3 0 1 0 0-6H9z" />
              </svg>
              </a>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};
export default Projects;
